import { assets } from '@/assets/assets'
import Image from 'next/image'
import React from 'react'
import { motion } from "motion/react"
import { TypeAnimation } from 'react-type-animation';

const Header = () =>{
  return (
    <div id='home' className='w-11/12 max-w-3xl text-center mx-auto h-screen flex flex-col items-center justify-center gap-4'>
        <motion.div
        initial={{scale:0}}
        whileInView={{scale:1}}
        transition={{duration:0.8, type: 'spring', stiffness: 100}}
        > 
          <Image src={assets.profile_img} alt='' className='rounded-full w-52' />
        </motion.div>
        <motion.h3 
        initial={{y:-20, opacity:0}}
        whileInView={{y:0, opacity:1}}
        transition={{duration:0.6, delay:0.3}}
        className='flex items-end gap-2 text-xl md:text-2xl mb-1 font-mono'>
          Hi! I'm Ron Oblepias <Image src={assets.hand_icon} alt='' className='w-6'/> </motion.h3>

            <TypeAnimation
              sequence={[
                'Frontend Developer',
                2000,
                'UI UX Designer',
                2000,
                'Graphic Designer',
                2000,
              ]}
              speed={10}
              className='text-2xl xs:text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-semibold'
              repeat={Infinity}
            />
              
              <motion.p 
                initial={{y:-20, opacity:0}}
                whileInView={{y:0, opacity:1}}
                transition={{duration:0.8, delay:0.7}}
              className='max-w-2xl mx-auto font- mt-4'>
             As a Frontend Developer, UI/UX Designer, and Graphic Designer, <br/>I blend creativity with technical expertise to build responsive, intuitive, and aesthetically pleasing interfaces.
             
              </motion.p>
          <div className='flex flex-col sm:flex-row items-center gap-4 mt-4'>
            <motion.a 
              initial={{y:30, opacity:0}}
              whileInView={{y:0, opacity:1}}
              transition={{duration:0.6, delay:1}}
            href="#contact" className='px-10 py-3 border border-white rounded-full bg-lightbrown text-white flex items-center gap-2 dark:bg-transparent font-outfit'
            >contact me <Image src={assets.right_arrow_white} alt='' className='w-4' />  </motion.a>

            <motion.a 
             initial={{y:30, opacity:0}}
             whileInView={{y:0, opacity:1}}
             transition={{duration:0.6, delay:1.2}}
            href="/sample-resume.pdf" download className='px-10 py-3 border rounded-full border-gray-500 flex items-center gap-2 bg-white dark:text-black font-outfit'
            >my resume <Image src={assets.download_icon} alt='' className='w-4' />  </motion.a>

          </div>
    </div>
  )
}

export default Header